import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTruckMoving, faCalendarAlt, faHandshake, faThumbsUp, faQuestionCircle, faTasks, faTools } from '@fortawesome/free-solid-svg-icons';

import styles from './AboutDescription.scss';
import certificate from "../../../assets/images/certificate_pl.jpg";

const description = [{icon: faHandshake, text: 'Jesteśmy firmą działającą na rynku od maja 1994 roku, co w branży produkcji opakowań daje nam duże doświadczenie. Dowodem tego są zadowoleni ze współpracy klienci oraz zdobywanie nowych rynków zbytu.'},
{icon: faTools, text: 'Inwestujemy w urządzenia ulepszające jakość naszych wyrobów. '},
{icon: faThumbsUp, text: 'Cechami, za które cenią nas klienci jest min: realizacja każdego nawet najmniejszego zamówienia, terminowość i elastyczność dostaw w zależności od potrzeb klienta. '},
{icon: faQuestionCircle, text: 'Przykładamy dużą wagę do profesjonalnej obsługi klienta, zapewniamy mu fachowe wsparcia na każdym etapie realizacji zamówienia.'},
{icon: faTasks, text: 'Pomagamy w wyborze optymalnych rozwiązań. Jesteśmy gotowi na podjęcie różnorodnych projektów, nawet tych najbardziej wymagających.'},
{icon: faTruckMoving, text: 'Zapewniamy własny elastyczny transport.'}];

const AboutDescription = () => {
    return (
    
    <section id="about_description">
    <div className='about_description_container'>
    <div className='about_description_img'></div>
    <div className='about_description_content' data-aos='fade-left'>
      {description.map((i, index) => {
      let myClass ='';

      if (index % 2 === 0) {
         myClass='description_content_part even'
         
       } else {
         myClass='description_content_part odd'
         
       }
        return <div className={myClass} key={index} >
            <span className="description_content_part_img">
                <FontAwesomeIcon icon={i.icon} />
            </span>
            <p className="description_content_part_text">
            {i.text}
            </p>
        </div>
      })}
        

    </div> 

    </div>
    
    </section>     
 
    );
};

export default AboutDescription;